<template>
    <div class="user-edit-container">
        <!-- Header 区域 -->
        <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" fixed/>

        <!-- 用户资料 -->
        <van-cell-group class="action-card"> 
            <van-cell title="头像" is-link center>
                <template #default>
                    <van-image round class="avatar" :src="profileObj.photo" @click="imageClickFn"/>
                    <!-- file 选择框 -->
                    <input type="file"
                    ref="iptFile"
                    v-show="false"
                    accept="image/*"
                    @change="onFileChange"
                    />
                </template>
            </van-cell>
            <van-cell title="名称" is-link :value="profileObj.name" @click="nameClickFn"/>
            <van-cell title="生日" is-link :value="profileObj.birthday" @click="birthdayClickFn"/>
        </van-cell-group>

        <!-- 姓名修改弹窗 -->
        <van-dialog v-model="show" title="修改名称" show-cancel-button :before-close="beforeCloseFn">
            <input type="text" v-fofo v-model="inputUserName">
        </van-dialog>

        <!-- 时间选择器 -->
        <van-popup round v-model="dateTimePickerShow" position="bottom" :style="{ height: '50%' }">
            <van-datetime-picker 
            v-model="currentDate" 
            type="date" 
            title="选择年月日" 
            :min-date="minDate" 
            :max-date="maxDate" 
            @cancel="cancelFn"
            @confirm="confirmFn"
            />
        </van-popup>
    </div>
</template>

<script>
import { userProfileAPI, updateUserPhotoAPI, updateUserProfileAPI } from '@/api'
import { Notify } from 'vant'
import { formatDate } from '@/utils/date.js'
import { mapMutations } from 'vuex'
export default {
    name: 'UserEdit',
    data() {
        return {
            profileObj: {}, // 用户基本资料 
            show: false,  // 姓名修改弹窗的出现
            inputUserName: '', // 修改名字 - 弹窗框输入框绑定的值
            minDate: new Date(1980, 0, 1),   // 最小范围
            maxDate: new Date(),  // 最大范围(默认系统今天日期)
            currentDate: new Date(),  // 当前时间
            dateTimePickerShow:false, // 时间选择器的显示/隐藏
        }
    },
    async created() {
        const res = await userProfileAPI()
        this.profileObj = res.data.data
    },
    methods: {
        ...mapMutations(['SET_USERPHOTO']),
        ...mapMutations(['SET_USERNAME']),

        // 文件选择改变 - 事件
        async onFileChange(e) {
            if (e.target.files[0].length === 0) return
            // 创建FormData对象
            const theFd = new FormData()
            // e.target.files[0] 用户选中的文件对象
            theFd.append('photo', e.target.files[0]) // 请求体里加入一对参数名和值

            const res = await updateUserPhotoAPI(theFd)
            this.profileObj.photo = res.data.data.photo
            this.SET_USERPHOTO(res.data.data.photo)
        },
        // 图片 - 点击事件
        imageClickFn() {
            this.$refs.iptFile.click() // 触发标签的事件
        },
        // 姓名 - 点击事件
        nameClickFn() {
            this.show = true
            this.inputUserName = this.profileObj.name
        },
        // 姓名 - 确认框 - 关闭前回调函数
        async beforeCloseFn(action, done) {
            if (action === 'confirm') {
                // 点确定
                let reg = /^[a-zA-Z0-9\u4e00-\u9fa5]{1,7}$/
                if (reg.test(this.inputUserName) === true) {
                    await updateUserProfileAPI({
                        name:this.inputUserName
                    })
                    this.profileObj.name = this.inputUserName
                    this.SET_USERNAME(this.profileObj.name)
                    done()
                } else {
                    Notify({ type: 'warning', message: '用户名只能是1-7位中英数组组合' })
                    done(false)
                }
            } else {
                // 点取消
                done()  // 让弹窗关闭 
            }
        },
        // 生日 - 点击事件
        birthdayClickFn() {
            this.dateTimePickerShow = true
            this.currentDate = new Date(this.profileObj.birthday)
        },
        // 日期选择器 - 取消事件
        cancelFn() { 
            this.dateTimePickerShow = false
        },
        // 日期选择器 - 确认事件
        async confirmFn() {
            await updateUserProfileAPI({
                birthday: formatDate(this.currentDate)
            })
            this.dateTimePickerShow = false
            this.profileObj.birthday = formatDate(this.currentDate)
        }
    }
}
</script>

<style scoped lang="less">
.user-edit-container{
    padding-top: 46px;
    /deep/.van-icon{
        color: white;
    }
    .avatar{
        width: 50px;
        height: 50px;
    }
}
/deep/ .van-dialog__content{
    text-align: center;
    input{
        padding: 0;
        margin: 10px 0;
        outline: none;
        border: none;
        text-align: center;
    }
}
</style>